.nutrition-block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 70%;
  .nutrition-article {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background-color: #fbfbfb;
    height: 20%;
    :nth-child(1) {
      grid-column: 1/2;
      grid-row: 1/3;
      justify-self: center;
      align-self: stretch;
    }
    :nth-child(2) {
      grid-column: 2/2;
      grid-row: 1/2;
      font-weight: 600;
      font-size: 20px;
      align-self: end;
      margin-block-end: inherit;
    }
    :nth-child(3) {
      grid-column: 2/2;
      grid-row: 2/2;
      color: #74798c;
      font-size: 14px;
      margin-block-start: inherit;
    }
  }
}
